* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  text-decoration: none;
}

/*HEADER*/

header {
  width: 100%;
  height: 80px;
  font-family: "Montserrat", sans-serif;
  position: fixed;
  top: 0px;
  right: 0;
  z-index: 100;
  background-image: url(./archivos/imagenes/fondofooter.png);
  background-size: cover;
  background-repeat: no-repeat;
  
}

.cont_header {
  max-width: 1600px;
  height: 100%;
  display: flex;
  justify-content: space-between;
  margin: auto;
  padding: 0px 20px;
}

header .logo {
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.logo img{
  width: 200px;
}

.cont_nav {
  position: relative;
  height: 100%;
}

nav {
  height: 100%;
}

  /*SE ACHICA EL HEADER CUANDO BAJAMOS*/
  .nav_mod{
    height: 70px;
    box-shadow: 1px 1px 10px 0px #88808059;
    
}
/*SE ACHICA EL HEADER CUANDO BAJAMOS*/
nav ul {
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

nav ul li {
 
  margin: 0px 100px;
  list-style: none;
  transition: margin-top 150ms; /*VELOCIDAD DE TRANSICION CUANDO PONGO EL CURSOR*/
}

nav ul li:hover {
  text-decoration: underline;
  margin-top: 5px;
  color: rgba(252, 160, 0, 0.664);
}
nav ul a:hover { color: rgba(252, 160, 0, 0.664);}
nav ul a {
  color: rgb(252, 161, 0);
  font-size: 20px;
  font-weight: 1000;
  text-decoration: none;
}

