/*INICIO FOOTER*/
footer {
  width: 100%;
  padding: 0px 0px;
  background-color: black;
  background-image: url(./archivos/imagenes/fondofooter.png);
  background-size: cover;
  background-repeat: no-repeat;
  height: 300px;
  color: rgb(252, 161, 0);
}


.container__footer {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  max-width: 1200px;
  margin: auto;
  margin-top: 10px;
  
}

.box__footer {
  display: flex;
  flex-direction: column;
  padding: 40px;
}

.box__footer .logo img {
  width: 180px;
}

.box__footer .terms {
  font-family: "Montserrat", sans-serif;
  max-width: 350px;
  margin-top: 20px;
  font-weight: 700;
  color: rgb(252, 161, 0);
  font-size: 18px;
}

.box__footer h2 {
  font-family: "Montserrat", sans-serif;
  margin-bottom: 20px;
  color: rgb(252, 161, 0);
  font-weight: 700;
}

.box__footer a {
  font-family: "Montserrat", sans-serif;
  margin-top: 10px;
  color: rgb(252, 161, 0);
  font-weight: 700;
}
.box__redes h2 {
  font-family: "Montserrat", sans-serif;
  margin-bottom: 25px;
  margin-top: 40px;
}

.terms p {
  font-weight: 600;
  font-family: "Montserrat", sans-serif;
}

.box__redes a {
  color: rgb(252, 161, 0);
  font-weight: 600;
  flex-direction: row;
  margin-inline: 5px;
  font-size: 25px;
}

.box__footer a:hover {
  opacity: 0.8;
}

.box__footer a .fab {
  font-size: 20px;
  flex-direction: column-reverse;
}
.box__footer i {
  font-size: 30px;
}
.box__copyright {
  max-width: 1200px;
  margin: auto;
  text-align: center;
  padding: 0px 40px;
}

.box__copyright p {
  font-family: "Montserrat", sans-serif;
  font-size: 15px;
  margin-top: 10px;
  color: rgb(252, 161, 0);
  font-weight: 600;
}

.box__copyright hr {
  border: none;
  height: 1px;
  background-color: rgb(252, 161, 0);
}
/*FIN FOOTER*/

/*boton de ir arriba*/
#button-up {
  width: 60px;
  height: 60px;
  background: rgb(252, 161, 0);
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  border-radius: 50%;
  font-size: 20px;
  position: fixed;
  bottom: 50px;
  right: 50px;
  cursor: pointer;
  border: 4px solid transparent;
  transition: all 300ms ease;
  transform: scale(0);
}

#button-up:hover {
  transform: scale(1.1);
  border-color: rgb(252, 161, 0);
}

